.apps__list {
  @include list;

  @media (min-width: $desktops) {
    margin-left: 13px;
  }
}

.apps {
  background-color: #f2f2f2;
  order: 2;

  @media (min-width: $tablets) {
    background-color: white;
  }

  @media (min-width: $desktops) {
    width: 435px;
  }
}

.form__fieldset--apps {
  padding: 0;

  @media (min-width: $tablets) {
    padding-top: 49px;
    padding-left: 57px;
    padding-bottom: 25px;
  }

  @media (min-width: $desktops) {
    width: 435px;
    padding-bottom: 10px;
  }
}

.form__legend--apps {
  padding: 38px 20px 28px;

  @media (min-width: $tablets) {
    margin: -15px 0;
    background-color: white;
    padding: 0 24px;
    margin-left: 65px;
  }

  @media (min-width: $desktops) {
    transform: translateX(-5px);
    margin-left: 0;
  }
}

input[type="radio"] {
  @include visually-hidden;
}

.apps__item {
  position: relative;
  padding: 11px 0 34px 0;

  &:last-child {
    padding-bottom: 48px;
  }
}

.apps__radio + label::before {
  content: "";
  position: absolute;
  width: 38px;
  height: 38px;
  background-color: white;
  border: 2px solid #e5e5e5;
  border-radius: 50%;
}

.apps__radio:hover + label::before {
  border: 2px solid #283645;
}

.apps__radio:active + label::before {
  border: 2px solid #d22856;
}

.apps__radio:checked + label::after {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  background-color: #d22856;
  border-radius: 50%;
  left: 11px;
  top: 22px;
}

.apps__text {
  font-size: 18px;
  line-height: 30px;
  padding-left: 70px;

  @media (min-width: $tablets) {
    padding-left: 60px;
    vertical-align: text-top;
  }
}
