body {
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header {
  position: absolute;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  color: #ffffff;
  z-index: 2; }
  @media (min-width: 960px) {
    .page-header {
      position: absolute;
      min-height: 110px;
      width: 100%;
      background-color: rgba(0, 0, 0, 0.3);
      color: #ffffff;
      z-index: 2; } }

.page-header--absolute {
  position: absolute; }

.page-header--relative {
  position: relative;
  background-color: #283645; }

.page-header__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0 auto; }
  @media (min-width: 960px) {
    .page-header__wrapper {
      width: 320px;
      padding-left: 20px;
      padding-right: 20px;
      margin: 0 auto;
      flex-direction: row; } }
  @media (min-width: 960px) and (min-width: 660px) {
    .page-header__wrapper {
      width: 660px; } }
  @media (min-width: 960px) and (min-width: 960px) {
    .page-header__wrapper {
      width: 960px;
      padding-right: 10px;
      padding-left: 10px; } }

.main-nav {
  width: 100%;
  margin: 0 auto;
  /*padding-top: 66px;*/ }
  @media (min-width: 660px) {
    .main-nav {
      width: auto;
      margin: 0;
      background-color: transparent;
      /*padding-top: 110px;*/ } }

.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 70px;
  height: 50px;
  background-color: transparent;
  cursor: pointer;
  border: none; }
  @media (min-width: 960px) {
    .main-nav__toggle {
      display: none; } }

.main-nav__toggle span {
  font-size: 0; }

.main-nav--closed .main-nav__toggle span {
  display: block;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 1px; }

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 13px;
  right: 14px;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 1px; }

.main-nav--closed .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 33px;
  right: 14px;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 1px; }

.main-nav--closed .main-nav__toggle:hover span, .main-nav--closed .main-nav__toggle:hover::before, .main-nav--closed .main-nav__toggle:hover::after,
.main-nav--closed .main-nav__toggle:active span, .main-nav--closed .main-nav__toggle:active::before, .main-nav--closed .main-nav__toggle:active::after {
  background-color: #d22856; }

/*
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 8px 0 0 rgba(255, 255, 255, 0.3), 0 16px 0 0 rgba(255, 255, 255, 0.3);
}*/
.main-nav--opened .main-nav__toggle span::before, .main-nav--opened .main-nav__toggle span::after {
  content: "";
  position: absolute;
  top: 17px;
  right: 17px;
  width: 29px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 1px; }

.main-nav--opened .main-nav__toggle span::before {
  transform: rotate(45deg); }

.main-nav--opened .main-nav__toggle span::after {
  transform: rotate(-45deg); }

.main-nav--opened .main-nav__toggle span:hover::before, .main-nav--opened .main-nav__toggle span:hover::after, .main-nav--opened .main-nav__toggle span:active::before, .main-nav--opened .main-nav__toggle span:active::after {
  background-color: #d22856; }

@media (max-width: 960px) {
  .main-nav--closed .main-nav__item {
    display: none; } }

.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-weight: bold;
  font-size: 18px;
  line-height: 32px;
  text-transform: uppercase;
  text-align: center;
  white-space: nowrap; }
  @media (min-width: 960px) {
    .main-nav__list {
      display: flex;
      flex-direction: row;
      justify-content: flex-end;
      line-height: 18px;
      font-weight: lighter;
      text-transform: none; } }

.main-nav__item {
  cursor: pointer;
  border-bottom: solid 1px black;
  border-top: solid 1px #485461;
  padding: 15px 95px; }
  @media (min-width: 960px) {
    .main-nav__item {
      margin-left: 84px;
      padding: 45px 0 36px 0; }
      .main-nav__item:first-child {
        margin-left: 170px; } }
  @media (min-width: 960px) and (min-width: 960px) {
    .main-nav__item--active {
      position: relative; }
      .main-nav__item--active::after {
        content: "";
        position: absolute;
        background-color: #ffffff;
        right: 0;
        bottom: 25px;
        left: 0;
        height: 2px;
        background: #ffffff; } }

.main-nav__item a {
  text-decoration: none;
  color: #ffffff;
  cursor: pointer; }

.main-nav__item:hover a {
  color: #d22856; }

