        height: 68px;
        background-size: 100% 68px; } }
    @media (min-width: 960px) {
      .page-footer__map-wrapper::before {
        height: 102px;
        background-size: 100% 102px; } }
  .page-footer__map-wrapper::after {
    content: "";
    position: absolute;
    top: 56%;
    left: 50%;
    width: 36px;
    height: 36px;
