.advantages__background {
  background-color: #f2f2f2;;
  position: relative;
}

.advantages__overview {
  position: relative;
  background-color: white;
  font-size: 24px;
  font-weight: lighter;
  line-height: 30px;
  text-align: center;
  padding: 0 85px 28px;

  @media (min-width: $tablets) {
    @include wrapper;
    font-size: 40px;
    line-height: 48px;
    padding-bottom: 48px;
    margin: 0 auto;
  }

  @media (min-width: $desktops) {
    font-size: 50px;
    line-height: 60px;
    margin: 0 auto 24px;
  }
}

.advantages__desc {
  margin: 40px auto 0;

  @media (min-width: $tablets) {
    margin: 48px auto 0;
  }

  @media (min-width: $desktops) {
    margin: 65px auto 0;
  }
}

.advantages__subtitle {
  margin: 25px auto 0;

  @media (min-width: $tablets) {
    margin: 0 auto;
    max-width: 355px;
  }

  @media (min-width: $desktops) {
    max-width: 450px;
  }
}

.advantages__wrapper {
  @include wrapper;
  position: relative;
  margin-top: 0;
  padding-top: 54px;

  @media (min-width: $desktops) {
    padding-top: 0;
  }
}

.advantages__background::before {
  content: "";
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background: url("../img/bg-triangle-white.svg") no-repeat center top;
  background-size: contain;
  z-index: 0;

  @media (min-width: $tablets) {
    top: 0;
  }
}
