.advantages__list {
  position: relative;
  padding-bottom: 30px;

  @media (min-width: $tablets) {
    display: flex;
    flex-direction: column;
    padding-left: 50%;
    padding-top: 3px;
  }

  @media (min-width: $desktops) {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0;
  }
}

.advantages__item {
  margin-top: 29px;

  @media (min-width: $tablets) {
    margin-top: 15px;
  }

  @media (min-width: $desktops) {
    margin-top: 0;
    padding: 0 20px;
    width: 300px;
  }
}

.advantages__title {
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  margin: 0;

  @media (min-width: $tablets) {
    text-align: left;
  }

  @media (max-width: $tablets-max) {
    br {
      display: none;
    }
  }

  @media (min-width: $desktops) {
    font-size: 30px;
    line-height: 30px;
    padding-top: 57px;
    text-align: center;
  }
}

.advantages__about {
  text-align: center;
  margin: 13px 0;

  @media (min-width: $tablets) {
    text-align: left;
    margin: 12px 0 19px;
  }

  @media (min-width: $desktops) {
    text-align: center;
    font-size: 18px;
    line-height: 30px;
    max-width: 300px;
    padding: 0 9px 0 5px;
    margin: 25px 0 18px;
  }
}


/*---КАРТИНКИ В ПРЕИМУЩЕСТВАХ---*/
.advantages__list::after {
  content: "";
  position: relative;
  display: block;
  top: 29px;
  width: 158px;
  height: 277px;
  margin-left: auto;
  margin-right: auto;
  background-image: url("../img/iphone-screen-mobile@1x.png");
  background-repeat: no-repeat;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/iphone-screen-mobile@2x.png");
    background-size: cover;
  }

  @media (min-width: $tablets) {
    display: none;
  }

  @media (min-width: $desktops) {
    display: none;
  }
}

.advantages__item--mood::before {
  @media (min-width: $tablets) {
    content: "";
    position: absolute;
    left: 0;
    top: -50px;
    width: 268px;
    height: 547px;
    margin-left: auto;
    margin-right: auto;
    background-image: url("../img/iphone-screen-3-tablet@1x.png");
    background-repeat: no-repeat;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/iphone-screen-3-tablet@2x.png");
      background-size: contain;
    }
  }
}

@media (min-width: $desktops) {
  .advantages__item--friends::before {
    content: "";
    position: relative;
    display: block;
    width: 100%;
    height: 302px;
    margin-left: auto;
    margin-right: auto;
    background-image: url("../img/iphone-screen-3-desktop@1x.png");
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/iphone-screen-3-desktop@2x.png");
    }
  }

  .advantages__item--mood::before {
    content: "";
    position: relative;
    display: block;
    top: 0;
    width: 100%;
    height: 302px;
    margin-left: auto;
    margin-right: auto;
    background-image: url("../img/iphone-screen-1-desktop@1x.png");
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/iphone-screen-1-desktop@2x.png");
    }
  }

  .advantages__item--world::before {
    content: "";
    position: relative;
    display: block;
    width: 100%;
    height: 302px;
    margin-left: auto;
    margin-right: auto;
    background-image: url("../img/iphone-screen-2-desktop@1x.png");
    background-repeat: no-repeat;
    background-position: center top;
    background-size: cover;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/iphone-screen-2-desktop@2x.png");
    }
  }
}

