.download {
  z-index: 1;

  @media (min-width: $desktops) {
    padding-top: 73px;
  }
}

.slogan__wrapper {
  /*width: 280px;*/
  display: flex;
  flex-direction: column;

  @media (min-width: $tablets) {
    width: 300px;
    padding-bottom: 80px;
  }

  @media (min-width: $desktops) {
    margin-top: 10px;
    padding-bottom: 90px;
  }
}

.download__slogan {

  font-size: 0;
  font-weight: 300;
  margin: 0;
  text-align: center;
  color: white;

  @media (min-width: $tablets) {
    font-size: 40px;
    line-height: 48px;
    padding-left: 45px;
    padding-right: 45px;
    margin-top: 26px;
    margin-bottom: 30px;
  }

  @media (min-width: $desktops) {
    font-size: 50px;
    line-height: 60px;
    padding: 0;
    margin-top: 26px;
    margin-bottom: 52px;
  }
}

.download__button-wrapper {
  margin: 20px 0 10px 0;

  @media (min-width: $tablets) {
    margin: 20px 0 47px 0;
  }

  @media (min-width: $desktops) {
    margin: 65px 0 60px 0;
  }
}

.download__button {
  font-weight: bold;
  font-size: 18px;
  color: white;
  cursor: pointer;
  border: 3px solid white;
  border-radius: 30px;
  background-color: transparent;
  padding: 13px 35px;

  &:hover {
    background-color: white;
    color: #d22856;
  }

  &:active {
    color: #f1becc;
  }

  @media (min-width: $tablets) {
    padding: 12px 45px;
    font-size: 18px;
  }

  @media (min-width: $desktops) {
    padding: 12px 28px;
    font-size: 21px;
  }
}

.download__list {
  @include list;
  font-size: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 35px 0 22px;

  @media (min-width: $tablets) {
    margin: 47px 0 37px 0;
  }

  @media (min-width: $desktops) {
    margin: 70px 0 55px 0;
  }
}

.download__item {
  @include link;
  width: 47px;
  height: 56px;
  margin-right: 16px;
  margin-left: 16px;
}
