.download__app {
  align-items: center;
  display: flex;
  flex-direction: column;
  text-align: center;
  background-color: $dark-background;

  @media (min-width: $tablets) {
    padding: 93px 0;
    width: 300px;
    background-color: transparent;
  }
}

.download__desc {
  color: white;
  text-align: center;
  padding-bottom: 30px;

  @media (min-width: $tablets) {
    font-size: 17px;
  }
}

